.header {
    z-index: 10;
    color: white;
    font-size: 1.5rem;
    width: 100%;
    height: var(--header-height);
    background-color: rgb(27, 25, 25);
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 3px 3px -2px rgba(0, 0, 0, 0.12), 0 1px 8px 0 rgba(0, 0, 0, 0.2);
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 0.75rem;
    .btnReturn {
        margin-right: auto;
        font-size: 0.8em;
        color: #ddd;
        border-radius: 5px;
        transition: color 0.2s;
        &:hover,
        &:active {
            color: white;
        }
    }

    .btnValidate {
        margin-left: auto;
        background-color: orange;
        padding: 0.25rem 0.5rem;
        border: none;
        font-size: 0.75rem;
        border-radius: 5px;
        color: white;
        font-weight: bold;
        text-transform: uppercase;
        outline: none;
        &.valid {
            background-color: green;
        }

        &:hover {
            cursor: pointer;
        }
    }

    .dictName {
        font-size: 1.5rem;
    }
}

@media (min-width: 800px) {
    .btnValidate {
        padding: 0.5rem 1rem !important; // for some reason the media query gets overwritten by rules above without important
        font-size: 1rem !important;
    }
}
